<template>
	<div>
		<div class="btn">
			<span>学校：</span>
			<el-select v-model="form.school" placeholder="请选择学校">
				<el-option v-for="item in schoolList" :key="item.id" :label="item.name" :value="item.id">
				</el-option>
			</el-select>
		</div>
		<div class="btn">
			<el-button type="primary" @click="addBuy">添加购买服务</el-button>
		</div>

		<el-dialog title="添加购买服务" :visible.sync="dialogBuy">
			<div class="box">
				<el-form :model="form" label-width="100px">
					<el-form-item label="选择学校">
						<el-select v-model="form.school" placeholder="请选择学校">
							<el-option v-for="item in schoolList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="选择服务">
						<el-checkbox v-model="checkAll" @change="handleCheckAllService">全选</el-checkbox>
						<div class="service">
							<div v-for="(item,index) in serviceList" :key="index">
								<el-checkbox-group v-model="form.service" class="service-item" >
									<el-checkbox :label="item" @change="oneChange">{{item.name}}</el-checkbox>
								</el-checkbox-group>
							</div>
						</div>
					</el-form-item>
					<el-form-item label="商品名称">
						<el-input v-model="form.title"></el-input>
					</el-form-item>
					<el-form-item label="商品价格">
						<el-input v-model="form.price"></el-input>
					</el-form-item>
					<el-form-item label="商品时长">
						<el-input v-model="form.time"></el-input>
					</el-form-item>
					<el-form-item label="商品描述">
						<el-input v-model="form.detail"></el-input>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newBuy">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>


		<el-table :data="tableDate">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="商品ID" align="center" width="100px"></el-table-column>
			<el-table-column prop="title" label="商品名称" align="center"></el-table-column>
			<el-table-column prop="price" label="商品价格" align="center"></el-table-column>
			<el-table-column prop="time" label="商品时长(天)" align="center"></el-table-column>
			<el-table-column prop="detail" label="商品描述" align="center"></el-table-column>
			<el-table-column prop="service[0].name" label="服务权限" align="center"></el-table-column>
			<el-table-column prop="created_at" label="创建时间" align="center"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center" width="300px">
				<template slot-scope="scope">
					<el-button size="mini" type="success" @click="handleEdit(scope.$index,scope.row)">编辑服务</el-button>
					<el-button size="mini" type="success" @click="handleOrder(scope.$index,scope.row)">服务订单</el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-sizes="[10, 20, 30, 40, 50]" :page-size="10" layout="sizes, prev, pager, next, jumper"
			 :total="totalPage" @size-change="handleSizeChange">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import API from '@/api//index.js'

	export default {
		name: 'buy',
		data() {
			return {
				schoolList: [],
				school: '',
				dialogBuy: false,
				checkAll: false, // 全选
				serviceIdList: [],
				serviceList: [{
					title: 'sendInfo',
					name: '消息推送'
				}, {
					title: 'classesNotice',
					name: '班级公告'
				}, {
					title: 'studentLocal',
					name: '学生定位'
				}, {
					title: "out-inRecord",
					name: '学生进出记录'
				}, {
					title: 'markInquire',
					name: '成绩查询'
				}, {
					title: 'buyLocator',
					name: '购买定位器'
				}],
				form: {
					title: '测试',
					price: '12',
					detail: '测试商品',
					service: [],
					school: '',
					time: ''
				},
				tableDate: [],
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getBuys();
			this.getSchool();
		},
		methods: {
			getBuys() {
				var self = this;
				API.buys(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			newBuy() {
				var self = this;
				if(self.form.price) {
					self.form.price = self.form.price * 100;
				}
				API.buy(self.form).then(res => {
					self.dialogBuy = false;
					self.$message.success('提交成功');
					self.getBuys();
					self.currentPage = 1;
					self.form = {};
					self.form.service = [];
				})
			},
			addBuy() {
				var self = this;
				self.dialogBuy = true;
			},
			// 获取学校列表
			getSchool() {
				var self = this;
				API.schools(self.currentPage, 100).then(res => {
					self.schoolList = res.data;
				})
			},
			// 全选服务
			handleCheckAllService(val) {
				var self = this;
				self.form.service = val ? self.serviceList : [],
				console.log(self.form.service)
			},
			oneChange() {
				var self = this;
				console.log(self.form.service)
			},
			// 操作
			handleEdit(index, row) {
				console.log(row.service)
				var self = this;
				self.dialogBuy = true;
				self.form = row;
				if(row.service.length === 6) {
					self.checkAll = true;
					row.service.forEach(item => {
						self.form.service.push({
							title: item.title,
							name: item.name
						})
					})
				}
			},

			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getBuys();
			},
			// 每页多少条
			handleSizeChange(val) {
				var self = this;
				API.buys(self.currentPage, val).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			}
		}
	}
</script>

<style scoped>
	.service {
		display: flex;
		flex-wrap: wrap;
	}
	
	.service-item {
		margin: 10px;
		padding: 0 10px;
	}
</style>
